import {Directive, HostListener} from '@angular/core';

@Directive()
export abstract class MobileComponent {
    isMobile = false;
    numberOfInfoContainers: number;

    @HostListener('window:resize')
    protected onResize() {
        this.isMobile = window.innerWidth <= 768;
        this.numberOfInfoContainers = this.getScreenSizeNumb(window.innerWidth);
    }

    getScreenSizeNumb(windowWidth: number): number {
        if (windowWidth >= 2500) {
            return 4;
        } else if (windowWidth >= 2000 && windowWidth < 2500) {
            return 4;
        } else if (windowWidth >= 1500 && windowWidth < 2000) {
            return 3;
        } else if (windowWidth >= 1000 && windowWidth < 1500) {
            return 2;
        } else if (windowWidth >= 700 && windowWidth < 1000) {
            return 2;
        } else if (windowWidth >= 500 && windowWidth < 700) {
            return 2;
        } else {
            return 2;
        }
    }
}
