import {MatMenuTrigger} from '@angular/material/menu';
import {Component, Input, ViewChild} from '@angular/core';
import {faTimes, IconDefinition} from '@fortawesome/free-solid-svg-icons';

import {LmrRouterLink} from '../../models/LmrRouterLink';
import {MobileComponent} from '../MobileComponent';

@Component({
    selector: 'app-navbar',
    templateUrl: './navbar.component.html',
    styleUrls: ['./navbar.component.scss']
})
export class NavbarComponent extends MobileComponent {

    @ViewChild(MatMenuTrigger) matMenuTrigger: MatMenuTrigger;
    @Input() navigationLinks: LmrRouterLink[];

    public enteredButton: boolean = false;
    public faClose: IconDefinition = faTimes;
    public lmrIcon: string = './assets/img/lmrLogo.png';
    public menuOpen: boolean = false;
    public prevButtonTrigger: MatMenuTrigger;
    public subMenuOpen: boolean = false;

    public constructor() {
        super();
        this.onResize();
    }

    public get title() {
        return this.isMobile ? 'LMR' : 'LMR Home Health Care';
    }

    public buttonEnter(trigger: MatMenuTrigger): void {
        setTimeout(() => {
            if (this.prevButtonTrigger && this.prevButtonTrigger !== trigger) {
                this.prevButtonTrigger.closeMenu();
                this.prevButtonTrigger = trigger;
                this.menuOpen = false;
                this.subMenuOpen = false;
                trigger.openMenu();
            } else if (!this.menuOpen) {
                this.enteredButton = true;
                this.prevButtonTrigger = trigger;
                trigger.openMenu();
            } else {
                this.enteredButton = true;
                this.prevButtonTrigger = trigger;
            }
        });
    }

    public buttonLeave(trigger: MatMenuTrigger): void {
        setTimeout(() => {
            if (this.enteredButton && !this.menuOpen) {
                trigger.closeMenu();
            }

            !this.menuOpen ? trigger.closeMenu() : this.enteredButton = false;
        }, 100);
    }

    public menuEnter(): void {
        this.menuOpen = true;
        if (this.subMenuOpen) {
            this.subMenuOpen = false;
        }
    }

    public menuLeave(trigger: MatMenuTrigger): void {
        setTimeout(() => {
            if (!this.subMenuOpen && !this.enteredButton) {
                this.menuOpen = false;
                trigger.closeMenu();
            } else {
                this.menuOpen = false;
            }
        }, 80);
    }

    public closeMenu(): void {
        this.matMenuTrigger.closeMenu();
    }

    public isMenuOpen(): boolean {
        if (!this.matMenuTrigger) {
            return false;
        }
        return this.matMenuTrigger.menuOpen;
    }
}
